@import "variables";
@import "mixins";

* {
    @include reset;
}

body {

    background-color: $bg-color;
    color: $text-color;

    a {
        color: $text-color;
        text-decoration: none;
        transition: .4s;
    }

    ul {
        list-style: none;
    }
}

.header {
    display: flex;
    justify-content: space-between;
    @include container;

    @include mobile {
        padding: 1.1rem;
    }

    &-brand {
        color: $primary-color;
        font-size: 3rem;
    }

    &-navbar {
        ul {
            height: 100%;
            @include flex-center;

            @include mobile {
                display: none;
            }

            li {
                margin: 0rem 1rem;
                text-transform: uppercase;
                font-size: 0.8rem;

                .active {
                    color: $secondary-color;
                }

                a {
                    &:hover {
                        color: $secondary-color;
                    }
                }
            }
        }
    }

    &-social-media {
        ul {
            @include flex-center;
            height: 100%;

            @include mobile {
                display: none;
            }

            li {
                margin: 0 1rem;

                a {
                    &:hover {
                        color: $primary-color;
                    }
                }
            }
        }
    }
}

.gallery {
    @include flex-center;
    @include container;

    @include mobile {
        flex-direction: column;
    }

    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0;

    &-card {
        width: 31%;
        height: 400px;
        margin-bottom: 4%;
        @include flex-center;
        flex-direction: column;
        color: $primary-color;

        @include mobile {
            width: 90%;
            margin-bottom: 6%;
        }


        &-tittle {
            font-size: 2rem;
            font-weight: 300;
            margin-bottom: 1rem;
        }

        &-btn {
            padding: .6rem 2rem;
            border: 2px solid $primary-color;
            color: $primary-color;
            font-weight: 400;
            text-transform: uppercase;
            letter-spacing: 1px;

            &:hover {
                background-color: $primary-color;
                color: $bg-color;
            }
        }
    }

    #card-1 {
        @include bg-img('sintra.jpg')
    }

    #card-2 {
        @include bg-img('lisboa.jpg')
    }

    #card-3 {
        @include bg-img('evora.jpg')
    }

    #card-4 {
        @include bg-img('elvas.jpg')
    }

    #card-5 {
        @include bg-img('porto.jpg')
    }

    #card-6 {
        @include bg-img('pipa.jpg')
    }

    #card-7 {
        @include bg-img('amesterdao.jpg')
    }

    #card-8 {
        @include bg-img('miami.jpg')
    }

    #card-9 {
        @include bg-img('saotome.jpg')
    }
}

footer {
    text-align: center;
    padding-bottom: 2rem;

    span {
        font-weight: bold;
        color: $secondary-color;
    }
}